}

@media (min-width: 1200px){
    .none{
        display: block;
      }

    .commands_container{
        width: 50%;
        margin:0 auto;
    }

    .commands_purpose_title{
        font-size:1.5rem;
        margin-top: 2rem;
    }  

    .commands_page_title{
        font-size:2rem;
    }
}